import Header from '../Header.jsx';
import Menuer from '../Menuer.jsx';
import '../Layout.css'

const BaseLayout = (props) => {
	return (
		<div className='view-BaseLayout'>
			<Header></Header>
			<div className='BaseLayout-mainer'>
				<div className='BaseLayout-l-side'>
				    <Menuer></Menuer>
				</div>
				<div className='BaseLayout-r-side'>
					{props.children}
				</div>
			</div>
		</div>
	);
};

export default BaseLayout;
